.container {
  max-width: 90rem;
  margin-left: auto;
  margin-right: auto;
}
.container-sm {
  padding: 0 1rem;
}
@media (min-width: 40em) {
  .container-sm {
    padding: 0 3.125rem;
  }
}
@media (min-width: 71.875em) {
  .container-sm {
    padding: 0 4.75rem;
  }
}
.container-md {
  padding: 0 1.25rem;
}
@media (min-width: 40em) {
  .container-md {
    padding: 0 3.125rem;
  }
}
@media (min-width: 71.875em) {
  .container-md {
    padding: 0 6.25rem;
  }
}
.container-lg {
  padding: 0 3.5rem;
}